body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-animation: fadeInAnimation ease 3s;
          animation: fadeInAnimation ease 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
    }
}

@keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
    }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  color: #A264DB;
}

.hover-glow:hover {
  box-shadow: 0px 0px 20px 0px rgb(168, 167, 165);
  transition: all .3s ease;
}

p {
  line-height: 1.5em;
}

.info-head {
  display: inline;
  font-weight: normal;
}

.container {
  padding: 10px;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

@media (max-width: 800px) {
  .project-col {
      flex-direction: column;
  }

  .container {
      padding: 5px;
  }
}

.info-link, .info-link:active {
  text-decoration: none;
  color: #A264DB;
}

.info-link:hover {
  text-decoration: underline;
}


/* Navbar Style */

.nav-bar {
    display: flex;
    justify-content: center;
    padding: 15px;
    font-size: larger;
}

nav a {
    text-decoration: none;
    color: black;
    transition: color .2s ease-out, background 1s ease-in;
}

nav a:hover,
nav a:active {
    color: #A264DB;
}

.active {
    color: #A264DB;
}

nav ul {
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}

nav li {
    display: inline;
    margin-right: 20px;
    margin-left: 20px;
}
/* Footer Style */

.footer {
    display: flex;
    padding: 20px;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
}

.iconify-inline {
    margin-right: 15px;
}

#copyright {
    margin-left: auto;
    padding-right: 50px;
    margin-top: 15px;
}
/* Header Style */

header {
    margin-top: 10px;
    padding: 10px;
    font-size: larger;
}

header h1 {
    text-align: center;
    font-weight: normal;
    letter-spacing: 3px;
}
.info {
    padding: 10px;
    margin-left: 30px;
    margin-right: 50px;

}

.home-text {
    max-width: 800px;
}

#avatar {
    padding: 20px;
    margin-right: 30px;
    margin-left: 60px;
}

.av {
    width: 300px;
    height: auto;
    max-height: 100%;
    max-width: 100%;
    border-radius: 50%;
    transition: all .3s ease;
}


.proj-img {
    width: 450px;
    height: 200px;
    margin-top: 15px;
    object-fit: cover;
    transition: all .3s ease;
}

.project-col {
    padding: 30px;
    padding-top: 0;
    max-width: 450px;
}

.tag {
    border-radius: 10px;
    border: none; 
    padding: 5px 8px 5px 8px;
    background-color: #d4acf8;
    font-weight: 700;
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.divider {
    width:10px;
    height:auto;
    display:inline-block;
}

